.greeting {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 250px;

  h2 {
    font-size: 2.65em;
    font-weight: 400;
  }
}

.experience-card {
  display: inline-block;
  // width: 375px;
  // max-width: 500px;
  // padding: 1em;
  .card {
    border: none;
    img.card-img-top {
      width: 350px;
      height: 125px;
      object-fit: contain;
      // padding: 2em;
    }
  }
}

.section-block {
  margin: 50px 0;
  // padding: 125px 0;
}

.project {
  img {
    width: 100%;
    height: 225px;
    object-fit: cover;
  }
  .icon {
    color: black;
    font-size: 3em;
  }
}
